.profile {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.profile-game-list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    overflow: auto;
}

.profile-container {
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    -ms-flex-align: center;
        align-items: center;
}

.profile-container .input {
    margin-top: 1em;
}

.profile-container .button {
    margin-top: 1em;
    height: 3em;
    width: 100%;

    justify-self: center;
}

.profile-container .button.logout {
    background-color: #b22222;
    color: #fafafa;
}

.profile-hotkey-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: .3em;
    margin-top: .5em;
}

.profile-hotkey {
    border: 1px solid #212121;
    height: 3em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-transition: 300ms all;
    -o-transition: 300ms all;
    transition: 300ms all;
    cursor: pointer;
}
.profile-hotkey:hover {
    border: 1px solid skyblue;
    -webkit-box-shadow: inset 0 0 3px skyblue;
    box-shadow: inset 0 0 3px skyblue;
}

.profile-hotkey.selected {
    border: 1px solid skyblue;
    
    -webkit-animation-name: selected-anim;
    
            animation-name: selected-anim;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
}
@-webkit-keyframes selected-anim {
    from {
        -webkit-box-shadow: inset 0 0 3px skyblue;
        box-shadow:         inset 0 0 3px skyblue;
    }
    to {
        -webkit-box-shadow: inset 0 0 30px skyblue;
        box-shadow:         inset 0 0 30px skyblue;
    }
}
@keyframes selected-anim {
    from {
        -webkit-box-shadow: inset 0 0 3px skyblue;
        box-shadow:         inset 0 0 3px skyblue;
    }
    to {
        -webkit-box-shadow: inset 0 0 30px skyblue;
        box-shadow:         inset 0 0 30px skyblue;
    }
}

.profile-hotkey p {
    font-size: 2em;
}



