.header-container {
    display: grid;
    grid-template-columns: 2fr repeat(4, 1fr);
    height: 96px;
    background-color: #2f54a3;
    -ms-flex-align: center;
        align-items: center;
}

.header-title {
    padding-left: 1em;
    color: #FAFAFA;
    font-size: 1em;
    text-decoration: none;
}

.header-link {
    color: #FAFAFA;
    font-size: 1em;
    text-decoration: none;
    cursor: pointer;
}

.header-link:hover {
    text-decoration: underline;
}

.header-link.active {
    text-decoration: underline;
}


