.login-container {
    background-color: #FFF;
    padding: .3em;
    width: 50vw;
    min-width: 300px;
}

.login-text {
    margin: .5em;
    display: block;
    text-align: center;
}

.login-text.guest {
    cursor: pointer;
    text-decoration: underline;    
    color: #555;
    -webkit-transition: 300ms all;
    -o-transition: 300ms all;
    transition: 300ms all;
}
.login-text.guest:hover {
    color: #212121;
}

.mdl-card.mdl-shadow--2dp {
    -webkit-box-shadow: none;
            box-shadow: none;
}



